#main > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10vh;
}

.header-container > div {
  display: flex;
  align-items: center;
}

.header-container svg {
  margin-right: 10px;
  width: 75px;
  height: 75px;
}

h1 {
  font-size: 48px;
}

.to-do-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  min-width: 20vw;
  border: solid 2px #808080;
}

.to-do-item > p {
  padding-left: 5px;
  min-width: 15vw;
  overflow-wrap: break-word;
  hyphens: auto;
}

.to-do-item > button {
  background: white;
  font-weight: bold;
}

/* Show a blue border when focusing on each button element tab because they aren't doing so by default with tabIndex */
.to-do-item > button:focus {
  border: 2px solid#2261C5;
}

form {
  display: flex;
  align-items: center;
  justify-content: center;
}

input {
  min-width: 20vw;
  height: 7vh;
  font-size: 14px;
}
